import React, { useState, useEffect, useContext } from 'react'
import Modal from 'react-bootstrap/Modal'
import { Form, Button, Dropdown } from 'react-bootstrap'
import { observer } from 'mobx-react-lite'
import { editSubTypeName, fetchTypes } from '../../../http/deviceAPI'
import { Context } from '../../../index'

const EditSubTypeName = observer(({ show, onHide }) => {
  const { device } = useContext(Context)
  const [value, setValue] = useState('')

  useEffect(() => {
    fetchTypes().then((data) => device.setTypes(data))
  }, [])

  const changeSubType = () => {
    editSubTypeName({ name: value, id: device.selectedSubType.id }).then(
      (data) => {
        setValue('')
        onHide()
      }
    )
  }

  return (
    <Modal show={show} onHide={onHide} centered>
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Змінити назву підтипу
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Dropdown className="mt-2 mb-2 admin-dropdown-buttons">
            <Dropdown.Toggle>
              {device.selectedSubType.name || 'Оберіть підтип'}
            </Dropdown.Toggle>
            <Dropdown.Menu style={{ overflow: 'auto', height: '300px' }}>
              {device.subtypes.map((subtype) => (
                <Dropdown.Item
                  onClick={() => device.setSelectedSubType(subtype)}
                  key={subtype.id}
                >
                  {subtype.name}
                </Dropdown.Item>
              ))}
            </Dropdown.Menu>
          </Dropdown>
          <Form.Control
            value={value}
            onChange={(e) => setValue(e.target.value)}
            placeholder={'Введіть нову назву підтипу товару'}
          />
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="outline-danger" onClick={onHide}>
          Відміна
        </Button>
        <Button variant="outline-success" onClick={changeSubType}>
          Підтвердити
        </Button>
      </Modal.Footer>
    </Modal>
  )
})

export default EditSubTypeName
