import React, { useState, useContext, useEffect } from 'react'
import Modal from 'react-bootstrap/Modal'
import { Button, Form, Dropdown } from 'react-bootstrap'
import { editBrandName, fetchTypes } from '../../../http/deviceAPI'
import { Context } from '../../../index'

const EditBrandName = ({ show, onHide }) => {
  const { device } = useContext(Context)
  const [value, setValue] = useState('')

  useEffect(() => {
    fetchTypes().then((data) => device.setTypes(data))
  }, [])

  const changeBrand = () => {
    editBrandName({
      newBrand: value,
      id: device.selectedBrand.id,
    }).then((data) => {
      setValue('')
      onHide()
    })
  }
  return (
    <Modal show={show} onHide={onHide} centered>
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Змінити назву бренду
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Dropdown className="mt-2 mb-2 admin-dropdown-buttons">
            <Dropdown.Toggle>
              {device.selectedBrand.name || 'Оберіть бренд'}
            </Dropdown.Toggle>
            <Dropdown.Menu style={{ overflow: 'auto', height: '300px' }}>
              {device.brands.map((brand) => (
                <Dropdown.Item
                  onClick={() => device.setSelectedBrand(brand)}
                  key={brand.id}
                >
                  {brand.name}
                </Dropdown.Item>
              ))}
            </Dropdown.Menu>
          </Dropdown>
          <Form.Control
            value={value}
            onChange={(e) => setValue(e.target.value)}
            placeholder={'Введіть нову назву бренду товару'}
          />
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="outline-danger" onClick={onHide}>
          Відміна
        </Button>
        <Button variant="outline-success" onClick={changeBrand}>
          Підтвердити
        </Button>
      </Modal.Footer>
    </Modal>
  )
}

export default EditBrandName
