import React, { useContext, useEffect, useState } from 'react'
import { Modal, Form, Dropdown, Button } from 'react-bootstrap'
import { Context } from '../../../index'
import {
  fetchBrands,
  fetchTypes,
  fetchSubTypes,
  fetchDevices,
  getSignedRequest,
  editDeviceImage,
  deleteImage,
} from '../../../http/deviceAPI'
import { observer } from 'mobx-react-lite'
import { v4 as uuidv4 } from 'uuid'
import axios from 'axios'

const EditDevicePhoto = observer(({ show, onHide }) => {
  const { device } = useContext(Context)
  const [file, setFile] = useState(null)
  const [deviceId, setDeviceId] = useState()

  useEffect(() => {
    fetchTypes().then((data) => device.setTypes(data))
    fetchSubTypes().then((data) => device.setSubTypes(data))
    fetchBrands().then((data) => device.setBrands(data))
    fetchDevices(
      device.selectedType.id,
      device.selectedSubType.id,
      device.selectedBrand.id,
      'all',
      'all'
    ).then((data) => {
      device.setDevices(data.rows)
    })
  }, [])

  const selectFile = (e) => {
    setFile(e.target.files[0])
  }

  const uploadFile = async (fileToUpload, url, signedRequest) => {
    try {
      const uploadResponse = await axios.put(signedRequest, fileToUpload, {
        headers: { 'Content-Type': fileToUpload.type },
      })
      if (uploadResponse.status === 200) {
        return true
      }
      return
    } catch (e) {
      console.log(e)
    }
  }

  const changeItemImage = async () => {
    try {
      const fileName = file.name + '_' + uuidv4()
      const response = await getSignedRequest({
        name: fileName,
        type: file.type,
      })

      if (response.url) {
        try {
          await uploadFile(file, response.url, response.signedRequest)
          await editDeviceImage({
            id: deviceId,
            newImgUrl: response.url,
          })
        } catch (e) {
          console.log(e)
        }
      }
    } catch (e) {
      console.log(e)
    }
  }
  const deleteOldImage = async () => {
    const baseUrl = `https://${process.env.REACT_APP_S3_BUCKET}.s3.amazonaws.com/`
    const startIndex = baseUrl.length
    const imageKey = device.selectedDevice.img.substring(startIndex)
    await deleteImage(imageKey)
  }

  const updateDeviceImage = () => {
    changeItemImage()
    deleteOldImage()
    device.setSelectedType({})
    device.setSelectedSubType({})
    device.setSelectedBrand({})
    device.setSelectedDevice({})
    onHide()
  }

  return (
    <Modal show={show} onHide={onHide} centered>
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Змінити фото товару
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Dropdown className="mt-2 mb-2 admin-dropdown-buttons">
            <Dropdown.Toggle>
              {device.selectedType.name || 'Обрати тип'}
            </Dropdown.Toggle>
            <Dropdown.Menu style={{ overflow: 'auto', height: '300px' }}>
              {device.types.map((type) => (
                <Dropdown.Item
                  onClick={() => device.setSelectedType(type)}
                  key={type.id}
                >
                  {type.name}
                </Dropdown.Item>
              ))}
            </Dropdown.Menu>
          </Dropdown>
          <Dropdown className="mt-2 mb-2 admin-dropdown-buttons">
            <Dropdown.Toggle>
              {device.selectedSubType.name || 'Обрати підтип'}
            </Dropdown.Toggle>
            <Dropdown.Menu style={{ overflow: 'auto', height: '300px' }}>
              {device.subtypes.map((subtype) =>
                device.selectedType.id === subtype.typeId ? (
                  <Dropdown.Item
                    onClick={() => device.setSelectedSubType(subtype)}
                    key={subtype.id}
                  >
                    {subtype.name}
                  </Dropdown.Item>
                ) : (
                  ''
                )
              )}
            </Dropdown.Menu>
          </Dropdown>
          <Dropdown className="mt-2 mb-2 admin-dropdown-buttons">
            <Dropdown.Toggle>
              {device.selectedBrand.name || 'Обрати виробника'}
            </Dropdown.Toggle>
            <Dropdown.Menu style={{ overflow: 'auto', height: '300px' }}>
              {device.brands.map((brand) =>
                device.selectedSubType.id === brand.subtypeId ? (
                  <Dropdown.Item
                    onClick={() => device.setSelectedBrand(brand)}
                    key={brand.id}
                  >
                    {brand.name}
                  </Dropdown.Item>
                ) : (
                  ''
                )
              )}
            </Dropdown.Menu>
          </Dropdown>
          <Dropdown className="mt-2 mb-2 admin-dropdown-buttons">
            <Dropdown.Toggle>
              {device.selectedDevice.name || 'Обрати товар'}
            </Dropdown.Toggle>
            <Dropdown.Menu style={{ overflow: 'auto', height: '300px' }}>
              {device.devices.map((item) =>
                device.selectedSubType.id === item.subtypeId &&
                device.selectedBrand.id === item.brandId ? (
                  <Dropdown.Item
                    onClick={() => {
                      device.setSelectedDevice(item)
                      setDeviceId(device.selectedDevice.id)
                    }}
                    key={item.id}
                  >
                    {item.name}
                  </Dropdown.Item>
                ) : (
                  ''
                )
              )}
            </Dropdown.Menu>
            <Form.Control className="mt-3" type="file" onChange={selectFile} />
          </Dropdown>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="outline-danger" onClick={onHide}>
          Відміна
        </Button>
        <Button variant="outline-success" onClick={() => updateDeviceImage()}>
          Підтвердити
        </Button>
      </Modal.Footer>
    </Modal>
  )
})

export default EditDevicePhoto
