import React, { useContext, useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom' // можна динамически передвигатся по страницам
import { Context } from '../index'
import axios from 'axios'
import { fetchUser } from '../http/userAPI'
import { CONFIRMATION_ROUTE } from '../utils/consts'
import jwtDecode from 'jwt-decode'
// import { v4 as uuidv4 } from 'uuid'
import { checkout } from '../http/basketAPI'

import creditCardSvg from '../assets/creditCardSvg.svg'
import cashPayment from '../assets/cashPayment.svg'

const telegramEndpoint = `https://api.telegram.org/bot${process.env.REACT_APP_TELEGRAM_TOKEN}`

const CheckOut = () => {
  const navigate = useNavigate()
  // const server_url = 'http://localhost:5000/api/basket/liqpay-response'
  const { basket, user } = useContext(Context)
  const [name, setName] = useState('')
  const [phone, setPhone] = useState('')
  const [email, setEmail] = useState('')
  const [disabled, setDisabled] = useState(true)
  const [liqPayData, setLiqPayData] = useState()
  const [liqPaySignature, setLiqPaySignature] = useState()

  useEffect(() => {
    if (user.isAuth) {
      const userEmail = jwtDecode(localStorage.token).email
      if (userEmail) {
        const userInfo = fetchUser(userEmail)
        userInfo.then((data) => {
          setName(data.userInfo.firstName + ' ' + data.userInfo.secondName)
          setPhone(data.userInfo.phone)
          setEmail(data.userInfo.email)
        })
      }
    }
  }, [liqPayData])

  const handlePhone = (e) => {
    setPhone(e.target.value)
  }

  const handleName = (e) => {
    setName(e.target.value)
  }

  const handleEmail = (e) => {
    setEmail(e.target.value)
  }

  const basketEdit = () => {
    basket.setIsOpenBasket(true)
  }

  // const modalOpen = () => {
  //   let modal = document.querySelector('.confirmation-container-hidden')
  //   modal.classList.remove('confirmation-container-hidden')
  //   modal.classList.add('confirmation-container')
  // }

  // const modalClose = () => {
  //   let modal = document.querySelector('.confirmation-container')
  //   modal.classList.remove('confirmation-container')
  //   modal.classList.add('confirmation-container-hidden')
  // }

  const saveInfo = () => {
    if (name && email && phone) {
      const element = document.querySelector('.order-button')
      setDisabled(false)
      element.classList.remove('disabled-order-btn')
    }
  }

  const placeOrder = async () => {
    try {
      let finalString = ''
      basket._basketContent.map((element) => {
        return (finalString +=
          `✅${element.name} - ${element.totalCount} ${element.purchaseUnit} - ${element.price}грн/${element.purchaseUnit}` +
          '\n')
      })
      await axios.post(`${telegramEndpoint}/sendMessage`, {
        chat_id: process.env.REACT_APP_CHAT_ID,
        parse_mode: 'HTML',
        text: `
       <b>Ім'я:</b> ${name}\n<b>Телефон:</b> ${phone}\n<b>Email:</b> ${email}\n<b>Замовив:</b>\n${finalString}\n<b>Всього: </b>${basket.BasketTotalAmount} грн`,
      })
    } catch (e) {
      console.log(e)
    }
  }

  const doPayment = async () => {
    try {
      const response = await checkout(
        basket.BasketTotalAmount,
        basket.BasketContent
      )
      setLiqPayData(response.data)
      setLiqPaySignature(response.signature)

      await placeOrder()
    } catch (e) {
      console.log(e)
    }
  }

  const resetBasket = () => {
    basket.setBasketContent([])
    basket.setBasketTotalAmount(0)
    basket.setBasketTotalItems(0)
    localStorage.setItem('basketTotalItems', 0)
    localStorage.setItem('basketTotalAmount', 0)
    localStorage.setItem('basket', JSON.stringify([]))
  }

  const handleCashPayment = async () => {
    await placeOrder()
    resetBasket()
    navigate(CONFIRMATION_ROUTE)
    setTimeout(() => {
      navigate('/')
    }, 4000)
  }

  return (
    <div className="checkout-container">
      <div className="checkout-wrapper">
        <div className="contacts">
          <div className="contact-info">
            <div className="checkout-header">
              <div className="checkout-header-icon"></div>
              <div className="checkout-header-text">Контакти</div>
            </div>

            <div className="contact-info-wrapper">
              <div className="checkout-user-phone-number">
                <div className="checkout-user-phone">
                  Номер для зв’язку та SMS сповiщень
                </div>
                <input
                  onChange={handlePhone}
                  type="text"
                  className="phone-number-input"
                  placeholder="Введіть ваш номер телефону"
                  required
                  value={phone}
                />
              </div>
              <div className="checkout-user-contacts-profile">
                <div className="checkout-user-contacts">
                  <div className="checkout-user-email-wrapper">
                    <div className="form-input-wrapper">
                      <label htmlFor="email" className="label-for">
                        Email для листування <span>*</span>
                      </label>
                      <div className="form-input">
                        <input
                          onChange={handleEmail}
                          placeholder="Ваша електронна пошта"
                          value={email}
                          type="email"
                          name="email"
                          className="email-input"
                        />
                      </div>
                    </div>
                  </div>
                  <div className="checkout-user-profile-name">
                    <div className="form-input-wrapper">
                      <label htmlFor="firstName" className="label-for">
                        Ім'я <span>*</span>
                      </label>
                      <div className="form-input">
                        <input
                          onChange={handleName}
                          placeholder="Ваше ім'я"
                          type="text"
                          name="firstName"
                          className="name-input"
                          value={name}
                          required
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="checkout-user-contacts-btn">
                <button className="save-customer-info-btn" onClick={saveInfo}>
                  Зберегти зміни
                </button>
              </div>
            </div>
          </div>
        </div>

        <div className="checkout-basket-preview">
          <div className="checkout-basket-container">
            <div className="basket-header">
              <div className="basket-header-title">
                <span className="basket-header-title-span">Кошик</span>
                {basket.BasketTotalItems !== 0 ? (
                  <span className="checkout-availabel-count">
                    {basket.BasketTotalItems}{' '}
                  </span>
                ) : (
                  ''
                )}
              </div>
              <div className="edit-button-wrapper">
                <button
                  className="checkout-edit-basket-btn"
                  onClick={basketEdit}
                >
                  Редагувати
                </button>
              </div>
            </div>
            <div className="checkout-item-info-wrapper">
              <div className="checkout-item-info">
                <span className="icon-iten-info"></span>
                <span className="item-text">Товари</span>
              </div>
              <div className="price-for-the-goods">
                {basket.BasketTotalAmount}.00 грн
              </div>
            </div>
            <div className="total-amount-container">
              <span className="total-amount-text">Всього:</span>
              <span className="total-amount-number">
                {basket.BasketTotalAmount}.00 грн
              </span>
            </div>
          </div>
          <div className="order-button-container">
            <button
              className="order-button disabled-order-btn"
              onClick={doPayment}
              disabled={disabled}
            >
              Продовжити
            </button>
          </div>
          {liqPayData ? (
            <div className="paymnet-option-container">
              <div className="payment-title">Оберіть спосіб оплати</div>
              <div className="pay-by-card">
                <form
                  method="POST"
                  action="https://www.liqpay.ua/api/3/checkout"
                  accept-charset="utf-8"
                >
                  <input type="hidden" name="data" value={liqPayData} />
                  <input
                    type="hidden"
                    name="signature"
                    value={liqPaySignature}
                  />
                  <button className="order-button" onClick={resetBasket}>
                    <img
                      src={creditCardSvg}
                      alt="cc"
                      style={{ width: '22px' }}
                    />{' '}
                    Оплатити картою онлайн
                  </button>
                </form>
              </div>
              <div className="pay-cash">
                <button className="order-button" onClick={handleCashPayment}>
                  <img src={cashPayment} alt="cc" style={{ width: '22px' }} />{' '}
                  Оплатити готівкою при отриманні
                </button>
              </div>
            </div>
          ) : (
            ''
          )}
        </div>
      </div>
    </div>
  )
}

export default CheckOut
