import React, { useState, useContext, useEffect } from 'react'
import { observer } from 'mobx-react-lite'
import { Context } from '../../../index'
import Modal from 'react-bootstrap/Modal'
import { Form, Button, Dropdown } from 'react-bootstrap'
import { fetchTypes, editTypeName } from '../../../http/deviceAPI'

const EditTypeName = observer(({ show, onHide }) => {
  const { device } = useContext(Context)
  const [value, setValue] = useState('')

  useEffect(() => {
    fetchTypes().then((data) => device.setTypes(data))
  }, [])

  const changeTypeName = ({ id, value }) => {
    editTypeName({ id, name: value }).then((data) => {
      setValue('')
      onHide()
    })
  }

  return (
    <Modal show={show} onHide={onHide} centered>
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">Додати тип</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Dropdown className="mt-2 mb-2 admin-dropdown-buttons">
            <Dropdown.Toggle>
              {device.selectedType.name || 'Обрати тип'}
            </Dropdown.Toggle>
            <Dropdown.Menu style={{ overflow: 'auto', height: '300px' }}>
              {device.types.map((type) => (
                <Dropdown.Item
                  onClick={() => device.setSelectedType(type)}
                  key={type.id}
                >
                  {type.name}
                </Dropdown.Item>
              ))}
            </Dropdown.Menu>
          </Dropdown>
          <Form.Control
            value={value}
            onChange={(e) => setValue(e.target.value)}
            placeholder={'Введіть нову назву типу товару'}
          />
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="outline-danger" onClick={onHide}>
          Відміна
        </Button>
        <Button
          variant="outline-success"
          onClick={() => changeTypeName({ id: device.selectedType.id, value })}
        >
          Підтвердити
        </Button>
      </Modal.Footer>
    </Modal>
  )
})

export default EditTypeName
