import React, { useContext } from 'react'
import '../../modals/Modal.css'
import { Context } from '../../../index'
import { observer } from 'mobx-react-lite'

const Modal = observer(({ setOpenModal, device }) => {
  const { basket } = useContext(Context)
  let newItem = true

  const basketService = {
    setTotalItems(total) {
      basket.setBasketTotalItems(total)
      localStorage.setItem('basketTotalItems', total)
    },
    setContent(state) {
      basket.setBasketContent(state)
      localStorage.setItem('basket', JSON.stringify(state))
    },
    setTotalAmount(amount) {
      basket.setBasketTotalAmount(amount)
      localStorage.setItem('basketTotalAmount', amount)
    },
  }

  const addToBasket = ({ name, price, img, purchaseUnit }) => {
    const temp = JSON.stringify(basket.BasketContent)
    let prevContent = JSON.parse(temp)

    if (prevContent != null) {
      for (let i = 0; i < prevContent.length; i++) {
        if (prevContent[i].name === name) {
          prevContent[i].totalCount += 1
          newItem = false
          break
        }
      }
    } else {
      prevContent = []
    }

    const totalAmount = +basket.BasketTotalAmount + +price
    if (newItem) {
      const totalItems = basket.BasketTotalItems + 1
      basketService.setTotalItems(totalItems)
      prevContent.push({ name, price, img, purchaseUnit, totalCount: 1 })
    }

    basketService.setTotalAmount(totalAmount)
    basketService.setContent(prevContent)

    setTimeout(() => {
      setOpenModal()
    }, 750)
  }

  return (
    <div className="modalBackground">
      <div className="modalContainer">
        <div className="titleCloseBtn">
          <button onClick={setOpenModal}>X</button>
        </div>
        <div className="item-wrapper">
          <div className="item-img">
            <img
              src={device.img}
              alt="device-img"
              style={{ maxWidth: '330px' }}
            />
          </div>
          <div className="item-info">
            <h1 className="item-name">{device.name}</h1>
            <p className="item-price">{device.price}.00 грн</p>
            <div className="button-wrapper">
              <button
                className="addToBasket"
                onClick={() => addToBasket(device)}
              >
                Зберегти до кошика
              </button>
            </div>
            <div className="item-comments">
              <p className="item-comment-label">Коментар для збирача</p>
              <textarea
                name="item-commet-body"
                id="item-comment-body"
                placeholder="Тут можна можна написати збирачу свої побажання до товару
               "
              ></textarea>
            </div>
          </div>
        </div>
        <div className="footer">
          Незважаючи на те, що ми докладаємо всіх зусиль, щоб вся інформація про
          продукти була актуальною і правильною, фото товару і опис товару,
          представлені на сайті, можуть відрізнятися від товару в торговому
          залі.
        </div>
      </div>
    </div>
  )
})

export default Modal
