import React, { useContext, useEffect, useState } from 'react'
import Modal from 'react-bootstrap/Modal'
import { v4 as uuidv4 } from 'uuid'
import { Button, Dropdown, Form } from 'react-bootstrap'
import { Context } from '../../../index'
import {
  createDevice,
  fetchBrands,
  fetchTypes,
  fetchSubTypes,
  getSignedRequest,
} from '../../../http/deviceAPI'
import { observer } from 'mobx-react-lite'
import axios from 'axios'

const CreateDevice = observer(({ show, onHide }) => {
  const { device } = useContext(Context)
  const [name, setName] = useState('')
  const [price, setPrice] = useState()
  const [weight, setWeight] = useState('')
  const [file, setFile] = useState(null)
  const [info, setInfo] = useState([])

  useEffect(() => {
    fetchTypes().then((data) => device.setTypes(data))
    fetchSubTypes().then((data) => device.setSubTypes(data))
    fetchBrands().then((data) => device.setBrands(data))
  }, [])

  //КАРТИНКА ЗАГРУЖАЄТЬСЯ НА AWS S3

  const uploadFile = async (fileToUpload, url, signedRequest) => {
    try {
      const uploadResponse = await axios.put(signedRequest, fileToUpload, {
        headers: { 'Content-Type': fileToUpload.type },
      })
      console.log('upload response is: ', uploadResponse)
    } catch (e) {
      console.log(e)
    }
  }

  const selectFile = (e) => {
    setFile(e.target.files[0])
  }

  const addDevice = async () => {
    const fileName = file.name + '_' + uuidv4()
    const response = await getSignedRequest({
      name: fileName,
      type: file.type,
    })

    if (response.url) {
      await uploadFile(file, response.url, response.signedRequest)

      const formData = new FormData()
      formData.append('name', name)
      formData.append('price', `${price}`)
      formData.append('imgUrl', response.url)
      formData.append('weight', weight)
      formData.append('brandId', device.selectedBrand.id)
      formData.append('typeId', device.selectedType.id)
      formData.append('subtypeId', device.selectedSubType.id)
      formData.append('info', JSON.stringify(info))
      formData.append('purchaseUnit', device.selectedPurchaseUnit.name)
      createDevice(formData).then((data) => {
        onHide()
        setName('')
        setWeight('')
        setPrice()
        setFile(null)
        setInfo([])
        device.setSelectedBrand({})
        device.setSelectedSubType({})
        device.setSelectedType({})
        device.setSelectedPurchaseUnit({})
      })
    } else {
      alert('Could not get signed URL.')
    }
  }

  return (
    <Modal show={show} onHide={onHide} centered>
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Додати товари
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Dropdown className="mt-2 mb-2 admin-dropdown-buttons">
            <Dropdown.Toggle>
              {device.selectedType.name || 'Обрати тип'}
            </Dropdown.Toggle>
            <Dropdown.Menu style={{ overflow: 'auto', height: '300px' }}>
              {device.types.map((type) => (
                <Dropdown.Item
                  onClick={() => device.setSelectedType(type)}
                  key={type.id}
                >
                  {type.name}
                </Dropdown.Item>
              ))}
            </Dropdown.Menu>
          </Dropdown>
          <Dropdown className="mt-2 mb-2 admin-dropdown-buttons">
            <Dropdown.Toggle>
              {device.selectedSubType.name || 'Обрати підтип'}
            </Dropdown.Toggle>
            <Dropdown.Menu style={{ overflow: 'auto', height: '300px' }}>
              {device.subtypes.map((subtype) =>
                device.selectedType.id === subtype.typeId ? (
                  <Dropdown.Item
                    onClick={() => device.setSelectedSubType(subtype)}
                    key={subtype.id}
                  >
                    {subtype.name}
                  </Dropdown.Item>
                ) : (
                  ''
                )
              )}
            </Dropdown.Menu>
          </Dropdown>
          <Dropdown className="mt-2 mb-2 admin-dropdown-buttons">
            <Dropdown.Toggle>
              {device.selectedBrand.name || 'Обрати виробника'}
            </Dropdown.Toggle>
            <Dropdown.Menu style={{ overflow: 'auto', height: '300px' }}>
              {device.brands.map((brand) =>
                device.selectedSubType.id === brand.subtypeId ? (
                  <Dropdown.Item
                    onClick={() => device.setSelectedBrand(brand)}
                    key={brand.id}
                  >
                    {brand.name}
                  </Dropdown.Item>
                ) : (
                  ''
                )
              )}
            </Dropdown.Menu>
          </Dropdown>
          <Dropdown className="mt-2 mb-2 admin-dropdown-buttons">
            <Dropdown.Toggle>
              {device.selectedPurchaseUnit.name || 'Обрати одиницю продажу'}
            </Dropdown.Toggle>
            <Dropdown.Menu style={{ overflow: 'auto', height: '300px' }}>
              {device.purchaseUnits.map((purchaseUnit) => (
                <Dropdown.Item
                  onClick={() =>
                    device.setSelectedPurchaseUnit({ name: purchaseUnit })
                  }
                  key={purchaseUnit}
                >
                  {purchaseUnit}
                </Dropdown.Item>
              ))}
            </Dropdown.Menu>
          </Dropdown>
          <Form.Control
            value={name}
            onChange={(e) => setName(e.target.value)}
            className="mt-3"
            placeholder="Введіть назву товару"
          />
          <Form.Control
            value={price}
            onChange={(e) => setPrice(Number(e.target.value))}
            className="mt-3"
            placeholder="Введіть вартість"
            type="number"
          />
          <Form.Control
            value={weight}
            onChange={(e) => setWeight(e.target.value)}
            className="mt-3"
            placeholder="Введіть вагу(об'єм) товару"
          />
          <Form.Control className="mt-3" type="file" onChange={selectFile} />
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="outline-danger" onClick={onHide}>
          Відміна
        </Button>
        <Button variant="outline-success" onClick={addDevice}>
          Підтвердити
        </Button>
      </Modal.Footer>
    </Modal>
  )
})

export default CreateDevice
