import React, { useState, useContext, useEffect } from 'react'
import Modal from 'react-bootstrap/Modal'
import { observer } from 'mobx-react-lite'
import { Button, Form, Dropdown } from 'react-bootstrap'
import { createBrand, fetchTypes, fetchSubTypes } from '../../../http/deviceAPI'
import { Context } from '../../../index'

const CreateBrand = observer(({ show, onHide }) => {
  const { device } = useContext(Context)
  const [value, setValue] = useState('')

  useEffect(() => {
    fetchTypes().then((data) => device.setTypes(data))
    fetchSubTypes().then((data) => device.setSubTypes(data))
  }, [])

  const addBrand = () => {
    createBrand({ name: value, subtypeId: device.selectedSubType.id }).then(
      (data) => {
        setValue('')
        onHide()
      }
    )
  }
  return (
    <Modal show={show} onHide={onHide} centered>
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Додати виробника
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Dropdown className="mt-2 mb-2 admin-dropdown-buttons">
            <Dropdown.Toggle>
              {device.selectedType.name || 'Оберіть тип'}
            </Dropdown.Toggle>
            <Dropdown.Menu style={{ overflow: 'auto', height: '300px' }}>
              {device.types.map((type) => (
                <Dropdown.Item
                  onClick={() => device.setSelectedType(type)}
                  key={type.id}
                >
                  {type.name}
                </Dropdown.Item>
              ))}
            </Dropdown.Menu>
          </Dropdown>
          <Dropdown className="mt-2 mb-2 admin-dropdown-buttons">
            <Dropdown.Toggle>
              {device.selectedSubType.name || 'Обрати підтип'}
            </Dropdown.Toggle>
            <Dropdown.Menu style={{ overflow: 'auto', height: '300px' }}>
              {device.subtypes.map((subtype) =>
                device.selectedType.id === subtype.typeId ? (
                  <Dropdown.Item
                    onClick={() => device.setSelectedSubType(subtype)}
                    key={subtype.id}
                  >
                    {subtype.name}
                  </Dropdown.Item>
                ) : (
                  ''
                )
              )}
            </Dropdown.Menu>
          </Dropdown>
          <Form.Control
            value={value}
            onChange={(e) => setValue(e.target.value)}
            placeholder={'Введіть назву виробника товару'}
          />
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="outline-danger" onClick={onHide}>
          Відміна
        </Button>
        <Button variant="outline-success" onClick={addBrand}>
          Підтвердити
        </Button>
      </Modal.Footer>
    </Modal>
  )
})

export default CreateBrand
