import React, { useState } from 'react'
import { Button } from 'react-bootstrap'
// import { useLocation, useNavigate } from 'react-router-dom'
import CreateBrand from '../components/modals/brandModals/CreateBrand'
import EditBrandName from '../components/modals/brandModals/EditBrandName'
import CreateDevice from '../components/modals/deviceModals/CreateDevice'
import CreateType from '../components/modals/typeModals/CreateType'
import EditTypeName from '../components/modals/typeModals/EditTypeName'
import EditSubTypeName from '../components/modals/subTypeModals/EditSubTypeName'
import CreateSubType from '../components/modals/subTypeModals/CreateSubType'
import EditDevicePrice from '../components/modals/deviceModals/EditDevicePrice'
import EditDevicePhoto from '../components/modals/deviceModals/EditDevicePhoto'
import DeleteDevice from '../components/modals/deviceModals/DeleteDevice'
import EditDeviceName from '../components/modals/deviceModals/EditDeviceName'
import EditDeviceWeight from '../components/modals/deviceModals/EditDeviceWeight'

const Admin = () => {
  const [brandVisible, setBrandVisible] = useState(false)
  const [typeVisible, setTypeVisible] = useState(false)
  const [deviceVisible, setDeviceVisible] = useState(false)
  const [subtypeVisible, setSubtypeVisible] = useState(false)
  const [editPriceVisible, setEditPriceVisible] = useState(false)
  const [editWeightVisible, setEditWeightVisible] = useState(false)
  const [deleteVisible, setDeleteVisible] = useState(false)
  const [editPhotoVisible, setEditPhotoVisible] = useState(false)
  const [editNameVisible, setEditNameVisible] = useState(false)
  const [editTypeNameVisible, setEditTypeNameVisible] = useState()
  const [editBrandVisible, setEditBrandVisible] = useState()
  const [editSubtypeVisible, setEditSubtypeVisible] = useState()

  return (
    <div className="admin-panel-wrapper">
      <div className="admin-subpanel-wrapper admin-type">
        <Button
          variant={'outline-dark'}
          className="mt-4 p-2 admin-panel-button"
          onClick={() => setTypeVisible(true)}
        >
          Додати тип товару
        </Button>
        <Button
          variant={'outline-dark'}
          className="mt-4 p-2 admin-panel-button"
          onClick={() => setEditTypeNameVisible(true)}
        >
          Змінити назву типу товарів
        </Button>
      </div>
      <div className="admin-subpanel-wrapper admin-subtype">
        <Button
          variant={'outline-dark'}
          className="mt-4 p-2 admin-panel-button"
          onClick={() => setSubtypeVisible(true)}
        >
          Додати підтип товару
        </Button>
        <Button
          variant={'outline-dark'}
          className="mt-4 p-2 admin-panel-button"
          onClick={() => setEditSubtypeVisible(true)}
        >
          Змінити назву підтипу товару
        </Button>
      </div>
      <div className="admin-subpanel-wrapper admin-brand">
        <Button
          variant={'outline-dark'}
          className="mt-4 p-2 admin-panel-button"
          onClick={() => setBrandVisible(true)}
        >
          Додати бренд
        </Button>
        <Button
          variant={'outline-dark'}
          className="mt-4 p-2 admin-panel-button"
          onClick={() => setEditBrandVisible(true)}
        >
          Змінити назву бренду
        </Button>
      </div>
      <div className="admin-subpanel-wrapper admin-device">
        <Button
          variant={'outline-dark'}
          className="mt-4 p-2"
          onClick={() => setDeviceVisible(true)}
        >
          Додати товар
        </Button>
        <Button
          variant={'outline-dark'}
          className="mt-4 p-2"
          onClick={() => setEditPriceVisible(true)}
        >
          Змінити ціну товару
        </Button>
        <Button
          variant={'outline-dark'}
          className="mt-4 p-2"
          onClick={() => setEditNameVisible(true)}
        >
          Змінити назву товару
        </Button>
        <Button
          variant={'outline-dark'}
          className="mt-4 p-2"
          onClick={() => setEditPhotoVisible(true)}
        >
          Змінити фото товару
        </Button>
        <Button
          variant={'outline-dark'}
          className="mt-4 p-2"
          onClick={() => setEditWeightVisible(true)}
        >
          Змінити вагу(об'єм) товару
        </Button>
        <Button
          variant={'outline-dark'}
          className="mt-4 p-2"
          onClick={() => setDeleteVisible(true)}
        >
          Видалити товар
        </Button>
      </div>

      <CreateBrand show={brandVisible} onHide={() => setBrandVisible(false)} />
      <EditBrandName
        show={editBrandVisible}
        onHide={() => setEditBrandVisible(false)}
      />
      <CreateDevice
        show={deviceVisible}
        onHide={() => setDeviceVisible(false)}
      />
      <CreateType show={typeVisible} onHide={() => setTypeVisible(false)} />
      <EditTypeName
        show={editTypeNameVisible}
        onHide={() => setEditTypeNameVisible(false)}
      />
      <CreateSubType
        show={subtypeVisible}
        onHide={() => setSubtypeVisible(false)}
      />
      <EditSubTypeName
        show={editSubtypeVisible}
        onHide={() => setEditSubtypeVisible(false)}
      />
      <EditDevicePrice
        show={editPriceVisible}
        onHide={() => setEditPriceVisible(false)}
      />
      <EditDeviceWeight
        show={editWeightVisible}
        onHide={() => setEditWeightVisible(false)}
      />
      <EditDeviceName
        show={editNameVisible}
        onHide={() => setEditNameVisible(false)}
      />
      <EditDevicePhoto
        show={editPhotoVisible}
        onHide={() => setEditPhotoVisible(false)}
      />
      <DeleteDevice
        show={deleteVisible}
        onHide={() => setDeleteVisible(false)}
      />
    </div>
  )
}

export default Admin
