import React, { useContext, useEffect } from 'react'
import TypeBar from '../components/TypeBar'
import BrandBar from '../components/BrandBar'
import DeviceList from '../components/DeviceList'
import { observer } from 'mobx-react-lite'
import { Context } from '../index'
import {
  fetchBrands,
  fetchDevices,
  fetchTypes,
  fetchSubTypes,
} from '../http/deviceAPI'
import Pages from '../components/Pages'

const Shop = observer(() => {
  const { device, basket } = useContext(Context)
  useEffect(() => {
    fetchTypes().then((data) => device.setTypes(data))
    fetchSubTypes().then((data) => device.setSubTypes(data))
    fetchBrands().then((data) => device.setBrands(data))
    fetchDevices(null, null, null, 1, 10).then((data) => {
      device.setDevices(data.rows)
      device.setTotalCount(data.count)
    })
  }, [])

  useEffect(() => {
    fetchDevices(
      device.selectedType.id,
      device.selectedSubType.id,
      device.selectedBrand.id,
      device.page,
      10
    ).then((data) => {
      device.setDevices(data.rows)
      device.setTotalCount(data.count)
    })
  }, [
    device.page,
    device.selectedType,
    device.selectedSubType,
    device.selectedBrand,
  ])

  /*prevent basket modal windows from scrolling when wv is less than 550px */
  useEffect(() => {
    const node = document.querySelector('#root')

    if (node && basket.IsOpenBasket === true && window.innerWidth < 550) {
      node.classList.add('root-no-scroll')
    } else if (
      node &&
      basket.IsOpenBasket === false &&
      window.innerWidth < 550
    ) {
      node.classList.remove('root-no-scroll')
    }
  }, [basket.IsOpenBasket])

  return (
    <div className="shop-main-container">
      <div className="main-container-row">
        <div className="typeBar-column">
          <TypeBar />
          <BrandBar />
        </div>
        <div className="brand-device-page-column">
          <DeviceList />
          <Pages />
        </div>
      </div>
    </div>
  )
})

export default Shop
