import React, { useState, useContext } from 'react'
import { Context } from '../index'
import { NavLink, useLocation, useNavigate } from 'react-router-dom'
import { Container, Form, Card, Button } from 'react-bootstrap'
import { LOGIN_ROUTE, REGISTRATION_ROUTE, SHOP_ROUTE } from '../utils/consts'
import { registration, login } from '../http/userAPI'
import { observer } from 'mobx-react-lite'

const Auth = observer(() => {
  const { user } = useContext(Context)
  const location = useLocation()
  const navigate = useNavigate()
  const isLogin = location.pathname === LOGIN_ROUTE
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [phone, setPhone] = useState('')
  const [firstName, setFirstName] = useState('')
  const [secondName, setSecondName] = useState('')

  const click = async () => {
    try {
      let data
      if (isLogin) {
        data = await login(email, password)
        // console.log(data)
      } else {
        data = await registration(email, password, phone, firstName, secondName)
        // console.log(data)
      }
      user.setIsAuth(true)
      user.setUser(user)

      navigate(SHOP_ROUTE)
    } catch (e) {
      alert(e.response.data.message)
    }
  }

  return (
    <Container className="auth-page-container">
      {isLogin ? (
        <div>
          {' '}
          <Card className="auth-page">
            <h2 className="m-auto">Авторизація</h2>
            <Form className="d-flex flex-column">
              <Form.Control
                className="mt-3"
                placeholder="Введіть email..."
                value={email}
                autoComplete="username"
                onChange={(e) => setEmail(e.target.value)}
              />
              <Form.Control
                className="mt-3"
                placeholder="Введіть пароль..."
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                type="password"
                autoComplete="current-password"
              />
              <div className="auth-option">
                <div>
                  Немає аккаунта?{' '}
                  <NavLink to={REGISTRATION_ROUTE}>Зареєструйтесь</NavLink>
                </div>

                <Button className="auth-button" onClick={click}>
                  Вхід
                </Button>
              </div>
            </Form>
          </Card>
        </div>
      ) : (
        <div>
          {' '}
          <Card style={{ width: 600 }} className="auth-page">
            <h2 className="m-auto">Реєстрація</h2>
            <Form className="d-flex flex-column">
              <Form.Control
                className="mt-3"
                placeholder="Введіть email..."
                value={email}
                autoComplete="username"
                onChange={(e) => setEmail(e.target.value)}
              />
              <Form.Control
                className="mt-3"
                placeholder="Введіть пароль..."
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                type="password"
                autoComplete="current-password"
              />
              <Form.Control
                className="mt-3"
                placeholder="Введіть свій номер телефону..."
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
                required
              />
              <Form.Control
                className="mt-3"
                placeholder="Введіть своє ім'я..."
                value={firstName}
                onChange={(e) => setFirstName(e.target.value)}
                required
              />
              <Form.Control
                className="mt-3"
                placeholder="Введіть своє прізвище..."
                value={secondName}
                onChange={(e) => setSecondName(e.target.value)}
                required
              />
              <div className="auth-option">
                <div>
                  Є аккаунт? <NavLink to={LOGIN_ROUTE}>Увійдіть</NavLink>
                </div>
                <Button className="auth-button" onClick={click}>
                  Реєстрація
                </Button>
              </div>
            </Form>
          </Card>
        </div>
      )}
    </Container>
  )
})

export default Auth
