import React, { useContext, useEffect } from 'react'
import { Context } from '../../../index'
import { observer } from 'mobx-react-lite'
import { v4 as uuidv4 } from 'uuid'
import { useNavigate } from 'react-router-dom' // можна динамически передвигатся по страницам
import { CHECKOUT_ROUTE, LOGIN_ROUTE } from '../../../utils/consts'

const Basket = observer(() => {
  const navigate = useNavigate()
  const { basket } = useContext(Context)

  const basketServices = {
    setContent(state) {
      basket.setBasketContent(state)
      localStorage.setItem('basket', JSON.stringify(state))
    },
    setTotalAmount(amount) {
      basket.setBasketTotalAmount(amount)
      localStorage.setItem('basketTotalAmount', amount)
    },
    setTotalItems(total) {
      basket.setBasketTotalItems(total)
      localStorage.setItem('basketTotalItems', total)
    },
    deleteItem(e) {
      let totalAmount = 0
      const newState = basket.BasketContent.filter(
        (item) => item.name !== e.target.dataset.name
      )
      basketServices.setContent(newState)
      newState.forEach((item) => (totalAmount += item.price * item.totalCount))
      let totalItems = basket.BasketTotalItems - 1
      basketServices.setTotalAmount(totalAmount)
      basketServices.setTotalItems(totalItems)
    },
    addItem(e) {
      const selectedItem = e.target.dataset.name
      let prevContent = basket.BasketContent
      let totalAmount = 0

      if (prevContent != null) {
        for (let i = 0; i < prevContent.length; i++) {
          if (prevContent[i].name === selectedItem) {
            prevContent[i].totalCount += 1
            totalAmount = basket.BasketTotalAmount + prevContent[i].price
            break
          }
        }
      } else {
        prevContent = []
      }

      basketServices.setTotalAmount(totalAmount)
      basketServices.setContent(prevContent)
    },
    addCustomInputCount(e) {
      const { value } = e.target
      if (Number(value) === 0) {
        basketServices.deleteItem(e)
      } else {
        const { name } = e.target.dataset
        let customAmount = Number(value)
        let prevContent = basket.BasketContent
        let totalAmount = 0

        if (prevContent != null) {
          for (let i = 0; i < prevContent.length; i++) {
            if (prevContent[i].name === name) {
              prevContent[i].totalCount = customAmount
              break
            }
          }
        } else {
          prevContent = []
        }

        prevContent.forEach(
          (item) => (totalAmount += item.totalCount * item.price)
        )
        basketServices.setTotalAmount(totalAmount)
        basketServices.setContent(prevContent)
      }
    },
    removetItem(e) {
      const selectedItem = e.target.dataset.name
      const prevContent = basket.BasketContent
      let totalAmount = 0
      for (let i = 0; i < prevContent.length; i++) {
        if (prevContent[i].name === selectedItem) {
          prevContent[i].totalCount -= 1
          totalAmount = basket.BasketTotalAmount - prevContent[i].price
          break
        }
      }
      basketServices.setTotalAmount(totalAmount)
      basketServices.setContent(prevContent)
    },
    closeBasket() {
      basket.setIsOpenBasket(false)
    },
    checkOut() {
      basket.setIsOpenBasket(false)
      document.querySelector('#root').classList.remove('root-no-scroll')
      // if (user.isAuth === true) {
      navigate(CHECKOUT_ROUTE)
      // } else {
      //   navigate(LOGIN_ROUTE)
      // }
    },
  }

  return (
    <div className="container">
      <div className="modalBackground">
        <div className="basketModalContainer">
          <div className="basketTitleCloseBtn">
            <div className="basket-title">Кошик</div>
            <div className="closeBtnWrapper">
              <button onClick={basketServices.closeBasket}>X</button>
            </div>
          </div>
          {basket.BasketTotalAmount === 0 && (
            <div style={{ textAlign: 'center', padding: '10px' }}>
              Ваш кошик пустий
            </div>
          )}
          {basket.BasketContent !== null &&
            basket.BasketContent.map((item) => (
              <div className="item-info-container" key={uuidv4()}>
                <div className="item-info-wrapper">
                  <div className="img-name-container">
                    <div className="basket-img-container">
                      <img src={item.img} alt="device-img" />
                    </div>
                    <div className="basket-item-name-container">
                      {item.name}
                    </div>
                  </div>
                  <div className="div"></div>
                  <div className="price-delete-btn-wrapper">
                    <div className="basket-item-price-container">
                      {item.price}.00 грн
                    </div>
                    <div className="delete-btn-wrapper">
                      <button
                        data-name={item.name}
                        style={{
                          border: 'none',
                          backgroundColor: 'transparent',
                        }}
                        onClick={basketServices.deleteItem}
                      >
                        x
                      </button>
                    </div>
                  </div>
                </div>
                <div className="item-count-container">
                  <div className="item-count-wrapper">
                    {item.totalCount >= 2 ? (
                      <button
                        className="basket-item-control-btn subtract-btn"
                        data-name={item.name}
                        onClick={basketServices.removetItem}
                      >
                        -
                      </button>
                    ) : (
                      <button
                        className="basket-item-control-btn delete-btn"
                        data-name={item.name}
                        onClick={basketServices.deleteItem}
                      >
                        x
                      </button>
                    )}
                    <div className="item-totalCount">
                      {item.purchaseUnit === 'кг' ? (
                        <input
                          className="customAmount"
                          maxLength="3"
                          size="1"
                          type="tel"
                          data-name={item.name}
                          data-unit={item.purchaseUnit}
                          defaultValue={item.totalCount}
                          onKeyPress={(event) => {
                            if (
                              !/[0-9]/.test(event.key) &&
                              !/\./.test(event.key)
                            ) {
                              event.preventDefault()
                            }
                          }}
                          onBlur={basketServices.addCustomInputCount}
                          style={{ border: 'none' }}
                        />
                      ) : (
                        <input
                          className="customAmount"
                          maxLength="3"
                          size="1"
                          type="tel"
                          data-name={item.name}
                          data-unit={item.purchaseUnit}
                          defaultValue={item.totalCount}
                          onKeyPress={(event) => {
                            if (
                              !/^[+-]?[1-9]\d?|[+-]?[0](?!\w)$/.test(event.key)
                            ) {
                              event.preventDefault()
                            }
                          }}
                          onBlur={basketServices.addCustomInputCount}
                          style={{ border: 'none' }}
                        />
                      )}
                      <span>{item.purchaseUnit}</span>
                    </div>
                    <button
                      className="basket-item-control-btn add-btn"
                      data-name={item.name}
                      onClick={basketServices.addItem}
                    >
                      +
                    </button>
                  </div>
                </div>
                <hr className="hr" />
              </div>
            ))}
          <hr className="hr basket-modal-final-hr" />
          <div className="basket-modal-footer">
            {basket.BasketTotalAmount === 0 ? (
              <div className="checkout-btn-wrapper">
                <button
                  className="checkout-btn"
                  onClick={basketServices.closeBasket}
                >
                  Перейти до покупок
                </button>
              </div>
            ) : (
              <div className="checkout-btn-wrapper">
                <button
                  className="checkout-btn"
                  onClick={basketServices.checkOut}
                >
                  Оформити
                </button>
              </div>
            )}
            <div className="totalAmountContainer">
              Всього: {basket.BasketTotalAmount.toFixed(2)} грн
            </div>
          </div>
        </div>
      </div>
    </div>
  )
})

export default Basket
